/**
 *  处置中心 mixin module
 *  @module base/mixin/disposalCenter
 *  @author coder
 */
import {
  getOrderList,
  getDisposalSelect,
  getDefectList,
  getDisposalTaskInfo,
  disposalFileUpload,
  forensicsProcess,
  getMonitorException,
  getProcessDetail,
  rectifyProcess,
  getProcessReviseTable,
  fileDownload
} from '@/base/api/disposal-center'

// mixin
export default {

  /**
   * 处置中心数据存储载体对象
   * @member disposalCenter
   * @property {Array} [list=[]] 获取处置中心列表响应数据
   * @property {number} [page=null] 获取处置中心列表响应页码
   * @property {number} [limit=null] 获取处置中心列表页大小，每页记录条数
   * @property {number} [total=0] 获取处置中心列表数据记录总条数
   * @property {object} [model=null] 获取处置中心单条记录
   */
  data() {
    return {
      disposalCenter: {
        list: [],
        page: null,
        limit: null,
        total: 0,
        model: null
      }
    }
  },
  methods: {

    /**
     * 处置中心---处置工单列表
     * @function getOrderList
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    getOrderList(data) {
      return getOrderList(data).then(res => {
        return res
      })
    },
    /**
     * 处置中心---下拉框数据
     * @function getDisposalSelect
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    getDisposalSelect(data) {
      return getDisposalSelect(data).then(res => {
        return res
      })
    },
    /**
     * 处置中心---获取缺陷清单列表
     * @function getDefectList
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    getDefectList(data) {
      return getDefectList(data).then(res => {
        return res
      })
    },
    /**
     * 处置中心---获取合规检查任务基本信息
     * @function getDisposalTaskInfo
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    getDisposalTaskInfo(data) {
      return getDisposalTaskInfo(data).then(res => {
        return res
      })
    },
    /**
     * 处置中心---文件上传接口
     * @function disposalFileUpload
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    disposalFileUpload(data) {
      return disposalFileUpload(data).then(res => {
        return res
      })
    },
    /**
     * 处置中心---取证阶段接口
     * @function forensicsProcess
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    forensicsProcess(data) {
      return forensicsProcess(data).then(res => {
        return res
      })
    },
    /**
     * 处置中心-缺陷清单列表-异常明细
     * @function getMonitorException
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    getMonitorException(data) {
      return getMonitorException(data).then(res => {
        return res
      })
    },
    /**
     * 处置中心---获取业务流程详情
     * @function getProcessDetail
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    getProcessDetail(data) {
      return getProcessDetail(data).then(res => {
        return res
      })
    },
    /**
     * 处置中心---(整改||不整改)阶段业务操作接口
     * @function rectifyProcess
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    rectifyProcess(data) {
      return rectifyProcess(data).then(res => {
        return res
      })
    },
    /**
     * 处置中心---查看关联修订已选列表接口
     * @function getProcessReviseTable
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    getProcessReviseTable(data) {
      return getProcessReviseTable(data).then(res => {
        return res
      })
    },
    /**
     * 处置中心---文件下载
     * @function fileDownload
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    fileDownload(data) {
      return fileDownload(data).then(res => {
        return res
      })
    }
  }
}
