// API_PATH
import {
  API_PATH
} from '@/config'

// ajax
import ajax from '@/utils/ajax'

/**
 * 处置中心---处置工单列表
 * @const
 * @type {String}
 * @default  '[API_PATH]/disposal/orders'
 */
export const GET_ORDER_LIST = API_PATH + '/disposal/orders'

/**
 * 处置中心---处置工单列表
 * @param {object} [data] 发送键值对数据
 * @returns {promise}
 */
export function getOrderList(data) {
  return ajax({
    method: 'POST',
    headers: {
      "Content-Type": "application/json;charset=utf8"
    },
    data: data,
    url: GET_ORDER_LIST
  })
}

